import { makeStyles } from "@material-ui/core";
import HeroImg from "../assets/hero.svg";
import HeroMDImg from "../assets/hero-md.svg";
import LinesImg from "../assets/lines.png";

const useStyles = makeStyles((theme) => ({
  "page-hero": {
    backgroundColor: "transparent",
    marginTop: "56px",
    paddingBottom: "56px",
    margin: "0 auto",
    maxWidth: "90%",
    position: "relative",
    zIndex: 99,

    "& .content": {
      backgroundImage: `url(${HeroImg})`,
      backgroundRepeat: "no-repeat",
      backgroundPosition: "top center",
      alignItems: "center",
      display: "flex",
      flexDirection: "column",
      justifyContent: "flex-end",
      height: "100%",

      "& .heading": {
        color: "#000000",
        fontWeight: 900,
        fontSize: "36px",
        lineHeight: "40px",
        marginTop: "142px",
        marginBottom: "28px",
        maxWidth: "320px",
        textAlign: "center",
      },

      "& .signup": {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        justifyContent: "space-between",
        marginBottom: "25px",

        "& .phone-input": {
          width: "auto!important",
          marginBottom: "10px",

          "& .flag-dropdown": {
            left: "0!important",

            "& .selected-flag .flag": {
              marginTop: "-12px !important",
              top: "50%!important",
            },
          },

          "& input": {
            backgroundColor: "#ffffff!important",
            border: "1px solid #000000!important",
            borderRadius: "8px!important",
            fontSize: "16px!important",
            fontWeight: 500,
            height: "48px!important",
            lineHeight: "24px!important",
            paddingTop: "0!important",
            paddingBottom: "0!important",
            width: "320px!important",
          },
        },

        "& button": {
          alignItems: "center",
          backgroundColor: "#12B0EE",
          borderRadius: "8px",
          boxShadow: "none",
          cursor: "pointer",
          display: "flex",
          fontSize: "14px",
          fontWeight: 900,
          lineHeight: "14px",
          height: "48px",
          justifyContent: "center",
          marginLeft: 0,
          padding: 0,
          textTransform: "capitalize",
          width: "125px",
        },
      },

      "& .features": {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        listStyle: "none",

        "& li": {
          alignItems: "center",
          color: "#000000",
          display: "flex",
          fontSize: "16px",
          fontWeight: 500,
          justifyContent: "center",
          lineHeight: "24px",
          paddingLeft: "22px",
          position: "relative",

          "&:not(:last-child)": {
            marginBottom: "12px",
          },

          "&::before": {
            content: '""',
            backgroundImage:
              'url("data:image/svg+xml,%0A%3Csvg%20width%3D%2216%22%20height%3D%2214%22%20viewBox%3D%220%200%2016%2014%22%20fill%3D%22none%22%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%3E%3Cpath%20d%3D%22M2%206.85802L5.50215%2011.0001L14%202.00006%22%20stroke%3D%22black%22%20stroke-width%3D%223%22%2F%3E%3C%2Fsvg%3E")',
            height: "13px",
            left: 0,
            position: "absolute",
            width: "16px",
          },
        },
      },
    },
  },

  "our-features": {
    margin: "0 auto",
    maxWidth: "90%",
    position: "relative",
    zIndex: 9,

    "& .content": {
      position: "relative",
      paddingBottom: "90px",

      "&::before": {
        content: '""',
        display: "block",
        filter: "blur(800px)",
        height: "100%",
        left: 0,
        position: "absolute",
        top: 0,
        width: "100%",
        zIndex: -99,
      },

      "& .intro": {
        display: "flex",
        flexDirection: "row",
        flexWrap: "wrap",
        justifyContent: "center",
        marginBottom: "48px",

        "& .data": {
          "& .description": {
            alignItems: "center",
            display: "flex",
            flexDirection: "column",
            height: "100%",
            paddingTop: "26px",
            textAlign: "center",

            "& .badge": {
              alignItems: "center",
              borderRadius: "30px",
              color: "#FFFFFF",
              display: "flex",
              fontSize: "24px",
              fontWeight: 900,
              height: "34px",
              justifyContent: "center",
              lineHeight: "24px",
              marginBottom: "18px",
              width: "128px",
            },

            "& .image": {
              marginBottom: "30px",
            },

            "& .title": {
              color: "#000000",
              fontSize: "48px",
              fontWeight: 900,
              lineHeight: "48px",
              marginBottom: "26px",
              maxWidth: "320px",
              textAlign: "center",
            },

            "& .info": {
              color: "#000000",
              fontSize: "18px",
              fontWeight: 500,
              lineHeight: "20px",
              marginBottom: "30px",
              maxWidth: "310px",
              textAlign: "center",
            },

            "& .action": {
              alignItems: "center",
              borderRadius: "8px",
              boxShadow: "none",
              color: "#ffffff",
              cursor: "pointer",
              display: "flex",
              fontSize: "14px",
              fontWeight: 900,
              lineHeight: "14px",
              height: "40px",
              justifyContent: "center",
              textDecoration: "none",
              textTransform: "capitalize",
              width: "115px",
            },
          },
        },
      },

      "& .features": {
        alignItems: "center",
        display: "flex",
        justifyContent: "space-evenly",
        flexDirection: "column",
        marginBottom: "20px",

        "& .block": {
          background: "#FFFFFF",
          borderRadius: "12px",
          display: "flex",
          flexDirection: "column",
          height: "318px",
          padding: "15px 20px",
          marginBottom: "42px",
          textAlign: "center",
          width: "300px",

          "& .info": {
            alignItems: "center",
            display: "flex",
            flexDirection: "column",
            flexGrow: 1,
            justifyContent: "space-evenly",

            "& h3": {
              color: "#24272C",
              fontSize: "36px",
              fontWeight: 900,
              lineHeight: "40px",
              maxWidth: "90%",
            },

            "& p": {
              color: "#24272C",
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "20px",
              maxWidth: "90%",
            },
          },
        },
      },

      "& .cta": {
        alignItems: "center",
        display: "flex",
        flexDirection: "column",
        justifyContent: "center",
        textAlign: "center",

        "& .header": {
          marginBottom: "32px",

          "& h4": {
            color: "#24272C",
            fontSize: "24px",
            fontWeight: 900,
            lineHeight: "30px",
          },
        },

        "& .actions": {
          display: "flex",
          flexDirection: "column",

          "& .explore": {
            alignItems: "center",
            backgroundColor: "#FFFFFF",
            borderRadius: "8px",
            display: "flex",
            fontSize: "14px",
            fontWeight: 900,
            lineHeight: "14px",
            height: "48px",
            justifyContent: "center",
            marginBottom: "22px",
            marginRight: 0,
            textDecoration: "none",
            transition: "all 0.2s ease",
            textTransform: "capitalize",
            width: "200px",
          },

          "& .demo": {
            alignItems: "center",
            borderRadius: "8px",
            boxShadow: "none",
            color: "#FFFFFF",
            display: "flex",
            fontSize: "14px",
            fontWeight: 900,
            lineHeight: "14px",
            height: "48px",
            justifyContent: "center",
            textDecoration: "none",
            textTransform: "capitalize",
            width: "200px",
          },
        },
      },
    },

    "&.engage": {
      "& .content": {
        "& .intro": {
          "& .data": {
            "& .description": {
              "& .badge": {
                backgroundColor: "#12B0EE",
                boxShadow: "0px 4px 10px #12B0EE",
              },

              "& .action": {
                backgroundColor: "#12B0EE",
              },
            },
          },
        },

        "& .features": {
          "& .block": {
            boxShadow: "0px 0px 40px 5px rgb(18, 176, 238, 0.2)",
          },
        },

        "& .cta": {
          "& .actions": {
            "& .explore": {
              border: "2px solid #12B0EE",
              color: "#12B0EE",

              "&:hover": {
                backgroundColor: "#12B0EE",
                color: "#ffffff",
              },
            },

            "& .demo": {
              backgroundColor: "#12B0EE",
            },
          },
        },
      },
    },

    "&.curate": {
      "& .content": {
        "& .intro": {
          "& .data": {
            "& .description": {
              "& .badge": {
                backgroundColor: "#FF53B0",
                boxShadow: "0px 4px 10px #FF53B0",
              },

              "& .action": {
                backgroundColor: "#FF53B0",
              },
            },
          },
        },

        "& .features": {
          "& .block": {
            boxShadow: "0px 0px 40px 5px rgba(237, 211, 225, 0.5)",
          },
        },

        "& .cta": {
          "& .actions": {
            "& .explore": {
              border: "2px solid #FF53B0",
              color: "#FF53B0",

              "&:hover": {
                backgroundColor: "#FF53B0",
                color: "#ffffff",
              },
            },

            "& .demo": {
              backgroundColor: "#FF53B0",
            },
          },
        },
      },
    },
  },

  "mid-banner-one": {
    backgroundColor: "#12B0EE",
    display: "flex",
    justifyContent: "center",
    height: "252px",
    padding: "42px 28px",
    position: "relative",
    marginBottom: "46px",
    textAlign: "center",
    zIndex: 99,

    "& h4": {
      color: "#ffffff",
      fontSize: "30px",
      fontWeight: 900,
      lineHeight: "34px",

      "& br": {
        display: "none",
      },
    },
  },

  "mid-banner-two": {
    backgroundColor: "#12B0EE",
    display: "flex",
    justifyContent: "center",
    height: "410px",
    padding: "70px 28px 75px",
    position: "relative",
    marginBottom: "75px",
    textAlign: "center",
    zIndex: 99,

    "& .content": {
      display: "flex",
      flexDirection: "column",
      height: "100%",
      justifyContent: "space-between",
    },

    "& h4": {
      color: "#ffffff",
      fontSize: "30px",
      fontWeight: 900,
      lineHeight: "32px",
    },

    "& .slider-container": {
      margin: "0 auto",
      maxWidth: "100vw",

      "& .swiper-slide": {
        alignSelf: "center",
      },
    },
  },

  enterprise: {
    backgroundColor: "transparent",
    margin: "0 auto 35px",
    maxWidth: "90%",
    mixBlendMode: "multiply",
    position: "relative",
    zIndex: 99,

    "& .content": {
      alignItems: "center",
      display: "flex",
      justifyContent: "center",
      margin: "0 auto",
      maxWidth: "1020px",

      "& .data": {
        alignItems: "center",
        maxWidth: "445px",
        display: "flex",
        flexDirection: "column",

        "& .title": {
          color: "#24272C",
          fontSize: "36px",
          fontWeight: 900,
          lineHeight: "40px",
          marginBottom: "12px",
          textAlign: "center",
        },

        "& .image": {
          marginBottom: "22px",

          "& .block-image": {
            maxWidth: "100%",
          },
        },

        "& .info": {
          color: "#24272C",
          fontSize: "18px",
          fontWeight: 500,
          lineHeight: "30px",
          margin: "0 10px 31px",
          textAlign: "justify",
        },

        "& .action": {
          alignItems: "center",
          backgroundColor: "#12B0EE",
          borderRadius: "8px",
          boxShadow: "none",
          color: "#ffffff",
          cursor: "pointer",
          display: "flex",
          fontSize: "14px",
          fontWeight: 900,
          lineHeight: "14px",
          height: "48px",
          justifyContent: "center",
          textDecoration: "none",
          textTransform: "capitalize",
          width: "135px",
        },
      },

      "& .image": {
        "& .block-image": {
          maxWidth: "100%",
        },
      },
    },
  },

  testimonials: {
    margin: "0 auto 160px",
    maxWidth: "90%",
    position: "relative",
    zIndex: 9,

    "& .content": {
      "& h4": {
        fontSize: "36px",
        fontWeight: 900,
        lineHeight: "40px",
        textAlign: "center",
        color: "#24272C",
        marginBottom: "40px",
      },

      "& .slider-container": {
        margin: "0 auto",
        maxWidth: "310px",
        padding: "0 6px",
        position: "relative",

        "& .swiper-slide": {
          height: "auto",

          "& .slide-card": {
            alignItems: "center",
            display: "flex",
            flexDirection: "column",
            height: "100%",
            justifyContent: "space-between",

            "& .slide-image": {
              marginBottom: "45px",
            },

            "& .info": {
              display: "flex",
              height: "100%",
              flexDirection: "column",
              maxWidth: "460px",

              "& .quote": {
                color: "#24272C",
                fontSize: "18px",
                fontWeight: 500,
                lineHeight: "20px",
                marginBottom: "33px",
              },

              "& .author": {
                color: "#24272C",
                fontSize: "16px",
                fontWeight: 500,
                lineHeight: "20px",
                marginTop: "auto",
              },
            },
          },
        },

        "& .slider-nav": {
          display: "flex",
          position: "absolute",
          right: "50%",
          top: "calc(100% + 30px)",
          transform: "translateX(50%)",

          "& .btn-swiper": {
            alignItems: "center",
            backgroundColor: "#ffffff",
            borderRadius: "50%",
            boxShadow: "0px 0px 20px rgba(165, 230, 255, 0.5)",
            color: "#12B0EE",
            cursor: "pointer",
            display: "flex",
            height: "62px",
            justifyContent: "center",
            padding: 0,
            margin: 0,
            width: "62px",

            "&.prev": {
              marginRight: "44px",
            },
          },
        },
      },
    },
  },

  values: {
    backgroundColor: "#F8D44D",
    borderRadius: "30px",
    margin: "0 auto 45px",
    maxWidth: "90%",
    padding: "35px 30px 50px",
    position: "relative",
    zIndex: 99,

    "& .content": {
      "& h4": {
        fontSize: "36px",
        fontWeight: 900,
        lineHeight: "40px",
        textAlign: "center",
        color: "#24272C",
        margin: "0 auto 40px",
      },

      "& .slider-container": {
        "& .swiper": {
          paddingBottom: "50px",

          "& .swiper-pagination-bullets": {
            bottom: 0,
            display: "flex",
            justifyContent: "center",

            "& .swiper-pagination-bullet": {
              backgroundColor: "#24272C",
              borderRadius: "10px",
              height: "7px",
              opacity: 1,
              width: "7px",
              transition: "width 0.2s ease",

              "&.swiper-pagination-bullet-active": {
                width: "23px",
              },
            },
          },
        },

        "& .slide-card": {
          alignItems: "center",
          display: "flex",
          flexDirection: "column",
          justifyContent: "center",
          maxWidth: "246px",
          margin: "0 auto",
          textAlign: "center",

          "& .image": {
            alignItems: "center",
            display: "flex",
            marginBottom: "19px",
            minHeight: "66px",
          },

          "& .info": {
            "& h5": {
              color: "#24272C",
              fontSize: "22px",
              fontWeight: "bold",
              lineHeight: "24px",
              marginBottom: "19px",
            },

            "& p": {
              color: "#24272C",
              fontSize: "16px",
              fontWeight: 500,
              lineHeight: "30px",
            },
          },
        },
      },
    },
  },

  intentions: {
    margin: "0 auto 45px",
    maxWidth: "90%",
    position: "relative",
    zIndex: 99,

    "& .content": {
      alignItems: "center",
      display: "flex",
      flexDirection: "column-reverse",
      justifyContent: "center",
      margin: "0 auto",
      maxWidth: "1020px",

      "& .data": {
        alignItems: "center",
        maxWidth: "430px",
        display: "flex",
        flexDirection: "column",

        "& .info": {
          fontSize: "18px",
          fontWeight: 500,
          lineHeight: "30px",
          margin: "0 10px",
          textAlign: "justify",
        },
      },

      "& .image": {
        "& .block-image": {
          maxWidth: "100%",
        },
      },
    },
  },
  [theme.breakpoints.up("xs")]: {},
  [theme.breakpoints.up("sm")]: {
    "page-hero, our-features, enterprise, testimonials, values, intentions": {
      maxWidth: "95%",
    },
  },
  [theme.breakpoints.up("md")]: {
    "page-hero": {
      marginTop: "67px",
      height: "738px",
      paddingBottom: "105px",
      width: "90%",
      maxWidth: "1100px",

      "& .content": {
        backgroundImage: `url(${HeroMDImg})`,

        "& .heading": {
          fontSize: "58px",
          lineHeight: "64px",
          marginBottom: "77px",
          maxWidth: "unset",
          width: "698px",
        },

        "& .signup": {
          flexDirection: "row",

          "& .phone-input": {
            marginBottom: 0,

            "& input": {
              marginBottom: 0,
              width: "363px!important",
            },
          },

          "& button": {
            height: "100%",
            marginLeft: "11px",
            width: "135px",
          },
        },

        "& .features": {
          flexDirection: "row",

          "& li": {
            "&:not(:last-child)": {
              marginBottom: 0,
              marginRight: "28px",
            },
          },
        },
      },
    },

    "our-features": {
      width: "90%",
      maxWidth: "1100px",

      "& .content": {
        "& .intro": {
          marginBottom: "80px",

          "& .image, & .data": {
            flex: "0 0 auto",
            width: "50%",
          },

          "& .data": {
            "& .description": {
              paddingTop: "64px",

              "& .badge": {
                marginBottom: "24px",
              },

              "& .title": {
                marginBottom: "18px",
                maxWidth: "532px",
              },

              "& .info": {
                marginBottom: "39px",
                maxWidth: "435px",
              },

              "& .action": {
                height: "48px",
                width: "164px",
              },
            },
          },
        },

        "& .features": {
          flexDirection: "row",
          marginBottom: 0,

          "& .block": {
            marginBottom: "90px",
          },
        },

        "& .cta": {
          "& .header": {
            marginBottom: "32px",

            "& h4": {
              color: "#24272C",
              fontSize: "24px",
              fontWeight: 900,
              lineHeight: "30px",
            },
          },

          "& .actions": {
            flexDirection: "row",

            "& .explore": {
              marginBottom: 0,
              marginRight: "24px",
              width: "150px",
            },

            "& .demo": {
              width: "150px",
            },
          },
        },
      },

      "&.engage": {
        "& .content": {
          "&::before": {
            backgroundColor: "#D7F4FF",
          },
        },
      },

      "&.curate": {
        "& .content": {
          "&::before": {
            backgroundColor: "#FFEEF7",
            zIndex: -999,
          },
        },
      },
    },

    "mid-banner-one": {
      backgroundImage: `url(${LinesImg})`,
      backgroundSize: "auto",
      backgroundRepeat: "repeat-x",
      backgroundPosition: "center calc(((99 + 131) / 270 ) * 100%)",
      height: "320px",
      padding: "50px 0",
      marginBottom: "58px",

      "& h4": {
        fontSize: "38px",
        lineHeight: "48px",

        "& br": {
          display: "block",
        },
      },
    },

    "mid-banner-two": {
      padding: "60px 0 60px",
      height: "350px",

      "& h4": {
        fontSize: "36px",
        lineHeight: "42px",
      },

      "& .slider-container": {
        maxWidth: "980px",
      },
    },

    enterprise: {
      width: "90%",
      marginBottom: "35px",
      maxWidth: "1100px",

      "& .content": {
        justifyContent: "space-between",

        "& .data": {
          alignItems: "flex-start",
          marginRight: "10px",

          "& .title": {
            fontSize: "48px",
            lineHeight: "48px",
            marginBottom: "33px",
            textAlign: "left",
          },

          "& .info": {
            margin: "0 0 33px",
            textAlign: "left",
          },
        },
      },
    },

    testimonials: {
      marginBottom: "100px",
      width: "90%",
      maxWidth: "1100px",

      "& .content": {
        position: "relative",

        "&::before": {
          backgroundColor: "#D7F4FF",
          content: '""',
          display: "block",
          filter: "blur(800px)",
          height: "100%",
          left: 0,
          position: "absolute",
          top: 0,
          width: "100%",
          zIndex: -99,
        },

        "& h4": {
          fontSize: "48px",
          lineHeight: "48px",
          marginBottom: "78px",
        },

        "& .slider-container": {
          maxWidth: "865px",
          padding: "0 106px",

          "& .swiper-slide": {
            "& .slide-card": {
              alignItems: "flex-start",
              flexDirection: "row",

              "& .slide-image": {
                marginBottom: 0,
                marginRight: "10px",
              },

              "& .info": {
                maxWidth: "460px",

                "& .quote": {
                  marginBottom: "50px",
                },
              },
            },
          },

          "& .slider-nav": {
            right: 0,
            top: "100%",
            transform: "unset",
          },
        },
      },
    },

    values: {
      marginBottom: "90px",
      padding: "64px 80px 75px",
      width: "90%",
      maxWidth: "1100px",

      "& .content": {
        "& h4": {
          fontSize: "48px",
          lineHeight: "48px",
          marginBottom: "85px",
          maxWidth: "485px",
        },

        "& .slider-container": {
          "& .swiper": {
            paddingBottom: 0,
          },
        },
      },
    },

    intentions: {
      width: "90%",
      marginBottom: "55px",
      maxWidth: "1100px",

      "& .content": {
        flexDirection: "row",
        justifyContent: "space-around",

        "& .data": {
          alignItems: "flex-start",

          "& .info": {
            margin: "0",
            textAlign: "left",
          },
        },
      },
    },
  },
  [theme.breakpoints.up("lg")]: {
    "page-hero, our-features, enterprise, testimonials, values, intentions": {
      maxWidth: "80%",
    },
  },
  [theme.breakpoints.up("xl")]: {
    "page-hero, our-features, enterprise, testimonials, intentions": {
      width: "70%",
    },
  },
}));

export default useStyles;
