import React, { Fragment } from "react";
import loadable from "@loadable/component";

// material-ui imports
import { useTheme, useMediaQuery } from "@mui/material";
// styles
import "react-phone-input-2/lib/material.css";
import "swiper/swiper-bundle.min.css";
import useStyles from "./styles/muiStyles";

const RH = loadable.lib(() => import("react-helmet"));

const Header = loadable(() => import("../components/Header"));
const Footer = loadable(() => import("../components/Footer"));

const Hero = loadable(() => import("./Hero"));
const Engage = loadable(() => import("./Engage"));
const MidBannerOne = loadable(() => import("./MidBannerOne"));
const MidBannerTwo = loadable(() => import("./MidBannerTwo"));
const Curate = loadable(() => import("./Curate"));
const Enterprise = loadable(() => import("./Enterprise"));
const Testimonial = loadable(() => import("./Testimonial"));
const Values = loadable(() => import("./Values"));
const Intentions = loadable(() => import("./Intentions"));

const Home = () => {
  const classes = useStyles(),
    theme = useTheme(), // theme setup
    isMobile = useMediaQuery(theme.breakpoints.down("sm")); // media query breakpoints

  return (
    <Fragment>
      {/* Head */}
      <RH fallback={null}>
        {({ Helmet }) => (
          <Helmet>
            <title>
              EasySocial - The best platform to quickly build Whastapp ChatBot
              and get Business API.
            </title>
            <link rel="canonical" href="https://easysocial.io/" />
            <meta
              name="keywords"
              content="Whatsapp chatbot ,automatic WhatsApp for business, Whatsapp AI, customizable whatsapp"
            />
            <meta
              name="description"
              content="EasySocial offers customizable and affordable WhatsApp chatbot solution for quick and hassle free functioning of your business."
            />
          </Helmet>
        )}
      </RH>
      {/* <Header /> */}
      <Header />
      {/* Page Hero */}
      <Hero className={classes["page-hero"]} />
      {/* Engage */}
      <Engage
        isMobile={isMobile}
        className={`${classes["our-features"]} engage`}
      />
      {/* Mid Banner 1 */}
      <MidBannerOne className={classes["mid-banner-one"]} />
      {/* Curate */}
      <Curate
        isMobile={isMobile}
        className={`${classes["our-features"]} curate`}
      />
      {/* Mid Banner 2 */}
      <MidBannerTwo className={classes["mid-banner-two"]} />
      {/* Enterprise */}
      <Enterprise className={classes["enterprise"]} isMobile={isMobile} />
      {/* Testimonials */}
      <Testimonial className={classes["testimonials"]} />
      {/* Values */}
      <Values className={classes["values"]} />
      {/* Intentions */}
      <Intentions className={classes["intentions"]} isMobile={isMobile} />
      {/* Footer */}
      <Footer />
    </Fragment>
  );
};

export default Home;
